<script>

  import $ from 'jquery'
  import mqtt from 'mqtt'
  import axios from 'axios'
  import '../assets/css/portal.css'
  import '../assets/js/waterbubble.js'
  import '../assets/css/styles.css'
  import '../assets/js/stopExecutionOnTimeout.js'
  import '../assets/js/jquery.knob.js'
  import lazyCount from '../assets/js/light.js'
  import ReconnectingWebSocket from '../assets/js/reconnecting-websocket.js'


  export default {
    name: 'portal',
    data () {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          if (this.ruleForm.passwordCheck !== this.ruleForm.password) {
            callback(new Error(''));
          }
          callback();
        }
      };
      return {
        note: {
          backgroundImage: "url(" + require("../../img/back.jpg") + ") ",
          backgroundPosition: "center center",
          backgroundRepeat: "no-repeat",
          backgroundSize: "cover",
        },
        date1: '',
        input:'',
        dialogVisible: false,
        manageVisible:false,
        editVisible:false,
        textInfo:'',
        waterData:'0.45',
        lightData:'0',
        random:'',
        //
        ruleForm:{},
        //
        form:{},
        tableData: [],
        registerData:[
          {'register':'','regValue':'','state':'ON'},
          // {'register':'','regValue':'','state':''},
          {'register':'','regValue':'','state':'LEFT'},
          {'register':'','regValue':'36361','state':'ON'},
          {'register':'Y','regValue':'','state':'OFF'},
          {'register':'','regValue':'','state':'ON'},],
        manageData:[],
        //socket
        websock:'',
        temsock:'',
        humidity:'',
        mqttConn:'ws://192.168.1.168:8831',
        //
        rules:
      {
        card:[
          {required: true, message: '', trigger: 'blur'},
        ],
        name: [
          {required: true, message: '', trigger: 'blur'},
        ],
        number:[
          {required: true, message: '', trigger: 'blur'},
        ],
        password:[
          {required: true, message: '', trigger: 'blur'},
        ],
        passwordCheck:[
          {required: true, message: '', validator: validatePass, trigger: 'blur'},
        ]
      }
    }
    },
    //
    mounted () {
      //
      this.myTime()
      // this.loadIp()
      setInterval(this.myTime, 1000)
      this.water()
      this.temper()
      this.rfidMessage()
      this.mqttTem()
      this.voiceTest()
      this.agvTest()
      this.registerTest()
    },
    methods: {
      //Ip
      loadIp(){
        this.$axios.get('static/mqtt.json')
          .then((res) => {
            console.log(res)
            this.mqttConn = res.data.MQTT.conn;
            console.log(this.mqttConn)
          }).catch((e) => {
          console.log(e);
        })
      },
      //[min,max]
      //Math.floor(Math.random()*minNum+1); 
       //parseInt(Math.random()*(max-min+1)+min,10);
      randomNum(min,max){
        switch(arguments.length){
          case 1:
            return Math.floor(Math.random()*minNum+1)
            break;
          case 2:
            return Math.floor(Math.random()*(max-min+1)+min)
            break;
          default:
            console.log('')
        break;
         }
      },
      //
      myTime () {
        let a = new Date()
        let b = a.toLocaleTimeString()
        let c = a.toLocaleDateString()
        if(c.split('/')[0].length==4){
          this.date1 = c.split('/')[0] + '  ' + c.split('/')[1] + '  ' + c.split('/')[2] + '  ' + '    ' + b
        }
        if(c.split('/')[2].length==4){
          this.date1 = c.split('/')[2] + '  ' + c.split('/')[0] + '  ' + c.split('/')[1] + '  ' + '    ' + b
        }

        },
      //
      water () {
        this.humidity = $('#waterbubble').waterbubble();
      },
      //
    changeWater(){
      this.humidity.setValue(this.waterData)
    },
      //
      temper () {
        $('#dial').knob()
      },
      //
      changeTemper(temper){
        $("#dial").val(temper);
        $("#dial").trigger('change');
      },
      //
      changeLight(){
        $("#p1_barPie").attr("data-to-value",this.lightData);
        $(".barPie__value").text(this.lightData);
        lazyCount();
      },
      //mqtt
      mqttTem(){
        $(".AGV").css({
          'transform':     'rotate(90deg)',});
        console.log('')
        var client = mqtt.connect(this.mqttConn); // you add a ws:// url here
        client.subscribe('sensor/temHum/#'); //  Topic
        var self = this
        client.on('message', function(topic, payload) {
          console.log(topic)
          var temObj = JSON.parse([payload].join(''));
          console.log(temObj)
          //
          self.temperInfo=temObj.temperature;
          self.changeTemper(self.temperInfo);
          //
          self.humiInfo=temObj.humidity;
          self.waterData = self.humiInfo/100
          self.changeWater();

          if(self.lightData!=self.humiInfo){
            self.lightData =self.humiInfo
            self.changeLight();
          }
        })
      },
      //rfid
      rfidMessage(){
        var client2 = mqtt.connect(this.mqttConn); // you add a ws:// url here
        client2.subscribe('sensor/rfidCheck/#'); //  Topic
        var self = this
        client2.on('message', function(topic, payload) {
          console.log(topic)
          var obj = JSON.parse([payload].join(''));
          console.log(obj)
          self.tableData.splice(0,0,{ number:obj.Employee_Number, name: obj.Name, permission: obj.Permissions, time: obj.checkTime.split('.')[0]})
          var utterThis = new window.SpeechSynthesisUtterance(obj.Name|+'');
          window.speechSynthesis.speak(utterThis);
          console.log(self.tableData)
        })
      },
    //  
      addInfo(){
        var random=this.randomNum(10000,100000000)
        var self = this
        var client = mqtt.connect(this.mqttConn); // you add a ws:// url here
        client.publish('request/rfidIdent', JSON.stringify({"randomValue":random}))
        client.subscribe('response/rfidIdent/+/' + random.toString()); //  Topic
        console.log('')
        client.on('message', function(topic, payload) {
          console.log(topic)
          var obj = JSON.parse([payload].join(''));
          console.log(obj)
          //
          if(obj.Card_Number===null)
          {
            self.$message('');
          }
          if(obj.Card_Number!=null&&obj.Employee_Number!=null){
            self.$message('');
          }
          if(obj.Card_Number!=null&&obj.Employee_Number===null) {
            self.dialogVisible = true
            self.ruleForm.card= obj.Card_Number
          }
          this.end();
        })
      },
    // 
      cancleAdd(){
        this.dialogVisible=false
        this.ruleForm={}
      },
      //
      sureAdd(formName){
        var self = this
        var random=self.randomNum(10000,100000000)
        self.$refs[formName].validate((valid) => {
          if (valid) {
            console.log(self.ruleForm)
            var client = mqtt.connect(self.mqttConn); // you add a ws:// url here
            client.publish('request/rfidManage', JSON.stringify({"Card_Number":self.ruleForm.card,"Employee_Number":self.ruleForm.number,"Password":self.ruleForm.password,"Name":self.ruleForm.name,"Permissions":'0',"Picture":'',"operation":'add',"randomValue":random}))
            client.subscribe('response/rfidManage/+/' + random.toString()); //  Topic
            console.log('')
            client.on('message', function(topic, payload) {
              console.log(topic)
              var obj = JSON.parse([payload].join(''));
              console.log(obj)
              //self.tableData.splice(0,0,{ number:obj.Employee_Number, name: obj.Name, permission: obj.Permissions})
              this.end();
            })
            self.$message('!');
            self.dialogVisible=false
            self.ruleForm={}
          } else {
            return false;
          }
        });
      },
      //
      manageInfo(){
        this.manageVisible=true
        this.manageData=[]
        var self = this
        var dataTable=[]
        var random=self.randomNum(10000,100000000)
        var client = mqtt.connect(self.mqttConn); // you add a ws:// url here
        client.publish('request/rfidQuery', JSON.stringify({"randomValue":random}))
        client.subscribe('response/rfidQuery/+/' + random.toString()); //  Topic
        console.log('')
        client.on('message', function(topic, payload) {
          console.log(topic)
          var obj = JSON.parse([payload].join(''));
          console.log(obj)
          var length = obj.data.length
          for(var i=0;i<length;i++){
            dataTable[i] = obj.data[i]
            // console.log(dataTable[i])
            self.manageData.push({
              "card": dataTable[i].Card_Number,
              "number": dataTable[i].Employee_Number,
              "name" : dataTable[i].Name,
              "permission": dataTable[i].Permissions,
              "picture": dataTable[i].Picture
            })
          }
          this.end();
        })
      },
      //
      cancleManage(){
        this.manageVisible=false
        this.manageData=[]
      },
      //
      sureManage(){
        this.manageVisible=false
        this.manageData=[]
      },
      //
      rfidEdit(index,row){
        console.log(row)
        this.manageVisible=false
        this.editVisible=true
        var self = this
        self.form.card = row.card
        self.form.name = row.name
        self.form.number = row.number
        self.form.permission = row.permission
        self.form.index = index
        console.log(self.form.index)
      },
      //
      readCard(){
        var random=this.randomNum(10000,100000000)
        var self = this
        var client = mqtt.connect(this.mqttConn); // you add a ws:// url here
        client.publish('request/rfidIdent', JSON.stringify({"randomValue":random}))
        client.subscribe('response/rfidIdent/+/' + random.toString()); //  Topic
        console.log('')
        client.on('message', function(topic, payload) {
          console.log(topic)
          var obj = JSON.parse([payload].join(''));
          console.log(obj)
          //
          if(obj.Card_Number===null)
          {
            self.$message('');
          }
          if(obj.Card_Number!=null&&obj.Employee_Number!=null){
            self.$message('');
          }
          if(obj.Card_Number!=null&&obj.Employee_Number===null) {
            self.$message("")
            self.form.card= obj.Card_Number
          }
          this.end();
        })

      },
      //
      cancleEdit(){
        this.manageVisible=true
        this.editVisible=false
      },
      //
      sureEdit(){
        var random=this.randomNum(10000,100000000)
        var self = this
        var client = mqtt.connect(self.mqttConn); // you add a ws:// url here
        client.publish('request/rfidManage', JSON.stringify({"Card_Number":self.form.card,"Employee_Number":self.form.number,"Password":self.form.password,"Name":self.form.name,"Permissions":self.form.permission,"Picture":'',"operation":'update',"randomValue":random}))
        client.subscribe('response/rfidManage/+/' + random.toString()); //  Topic
        console.log('')
        client.on('message', function(topic, payload) {
          console.log(topic)
          var obj = JSON.parse([payload].join(''));
          console.log(obj)
          if(obj.result===true){
            self.$message("")
            console.log(self.form.card)
            self.manageData[self.form.index].card=obj.Card_Number
            self.manageData[self.form.index].number=obj.Employee_Number
            self.manageData[self.form.index].name=obj.Name
            self.manageData[self.form.index].permission=obj.Permissions
            self.manageData[self.form.index].picture=obj.Picture
            self.manageVisible=true
            self.editVisible=false
          }
          else{
            self.$message(""+obj.msg)
          }
          this.end();
        })
      },
      //
      rfidDelete(row){
        console.log(row)
        var self = this
        var random=self.randomNum(10000,100000000)
        var client = mqtt.connect(self.mqttConn); // you add a ws:// url here
        client.publish('request/rfidManage', JSON.stringify({"Card_Number":row.card,"Employee_Number":row.number,"Password":row.password,"Name":row.name,"Permissions":row.permission,"Picture":row.picture,"operation":'delete',"randomValue":random}))
        client.subscribe('response/rfidManage/+/' + random.toString()); //  Topic
        console.log('')
        client.on('message', function(topic, payload) {
          console.log(topic)
          var obj = JSON.parse([payload].join(''));
          console.log(obj.result)
          if(obj.result===true){
            self.$message("")
            self.manageInfo()
          }
          else{
            self.$message("")
          }
          this.end();
        })
      },
      //
      startVoice(){
        this.textInfo="Start Test";
        // var random=self.randomNum(10000,100000000)
        var client = mqtt.connect(this.mqttConn); // you add a ws:// url here
        client.publish('request/voice', JSON.stringify({"command":"start"}));
      },
      //
      voiceTest(){
        var self = this
        // var random=self.randomNum(10000,100000000)
        var client = mqtt.connect(self.mqttConn); // you add a ws:// url here
        // client.publish('request/voice', JSON.stringify({"command":"start"}))
        client.subscribe('voice/recognizer/#'); //  Topic
        // console.log('')
        client.on('message', function(topic, payload) {
          console.log(topic)
          var obj = JSON.parse([payload].join(''));
          console.log(obj)
          self.textInfo ='command: '+ obj.command
        })
      },
      //AGV
      changeAGV(desTop,desLeft){
        var initTop=$(".AGV").position().top;
        var initLeft=$(".AGV").position().left;
        console.log(''+initTop+''+initLeft);
        var top = 257.6-desTop*76.9-26
        var left = 450+desLeft*76.9
        console.log('')
        console.log(''+top+''+left);
        $(".AGV").animate({top:top, left:left},400)
      },
      //AGV,75.7px/m
      agvTest(){
        var self = this
        var client = mqtt.connect(self.mqttConn); // you add a ws:// url here
        client.subscribe('sensor/agv/#'); //  Topic
        // console.log('AGV')
        client.on('message', function(topic, payload) {
          console.log(topic)
          var obj = JSON.parse([payload].join(''));
          console.log(obj);
          if(obj.state==true){
            var position = obj.data
            self.changeAGV(position.y,position.x)
          }
          else{
            console.log('')
          }
        })
      },
      //AGV
      AGVcommand(command){
        var self = this
        console.log(command)
        var client = mqtt.connect(self.mqttConn); // you add a ws:// url here
        client.publish('test', command)
      },

      tableRowClassName({row, rowIndex}) {
        // console.log(row);
        if (row.state === 'ON') {
          return 'warning-row';
        } else if (row.state === 'LEFT'||row.state === 'RIGHT') {
          return 'success-row';
        }
        return '';
      },

      //
      registerTest(){
        var self = this
        var client = mqtt.connect(self.mqttConn); // you add a ws:// url here
        client.subscribe('sensor/productLine/#'); //  Topic
        client.on('message', function(topic, payload) {
          console.log(topic)
          var obj = JSON.parse([payload].join(''));
          console.log(obj)
          if(obj.result==true){
            var data = obj.data
            self.registerData[0].regValue = data.R50000
            self.registerData[1].regValue = data.MR7001
            self.registerData[2].regValue = data.R40110
            self.registerData[3].regValue = data.LR402
            self.registerData[4].regValue = data.R34114
            if(Number(data.R50000)%2==1){
              self.registerData[0].state='ON'
            }
            else{
              self.registerData[0].state='OFF'
            }

            if(data.MR7001=='00001'){
              self.registerData[1].state='LEFT'
            }
            else if(data.MR7001=='00002'){
              self.registerData[1].state='RIGHT'
            }
            else if(data.MR7001=='00000'){
              self.registerData[1].state='STOP'
            }

            if(data.R40110=='36361'){
              self.registerData[2].state='ON'
            }
            else if(data.R40110=='36360'){
              self.registerData[2].state='OFF'
            }

            if(data.LR402=='00001'){
              self.registerData[3].state='ON'
            }
            else if(data.LR402=='00002'){
              self.registerData[3].state='OFF'
            }

            if(data.R34114=='00001'){
              self.registerData[4].state='ON'
            }
            else if(data.R34114=='00002'){
              self.registerData[4].state='OFF'
            }
          }
          else{
            console.log('')
          }
        })
      }
  }
  }
</script>



